@import "./reset.css";

:root {
  --background-color-one: rgb(255, 255, 255);
  --background-color-two: rgb(0, 0, 0);
  --dialog-background-color: rgba(0, 0, 0, 0.877);
  --font-color-one: rgb(240, 235, 235);
  --font-color-two: rgb(28, 26, 26);
  --footer-background: rgba(251, 251, 251, 0);
  --background-mask: rgba(0, 0, 0, 0.7);
  --action-button-color: #551985;
  --input-background-color: rgba(26, 26, 26, 0.863);
  --input-border-color: rgba(240, 235, 235, 0.831);
  --shadow: -16px 13px 19px -3px rgba(0, 0, 0, 0.122);
}


* {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  background-color: #fff;
  color: #000;
}


/* Auxilary classes */

.container {
  width: min(100% - 90px, 1080px);
  margin: 0 auto;
} 


.flex {
  display: flex;
}


.flex-wrap {
  flex-wrap: wrap;
}


.space-around {
  justify-content: space-around;
}


.space-between {
  justify-content: space-between;
}


.center {
  justify-content: center;
  text-align: center;
}


.gap-15 {
  gap: 15px;
}


.gap-40 {
  gap: 40px;
}

/*-------------*/
/*---Header----*/
/*-------------*/


header {
  height: 56px;
  width: 100%;
  color: var(--font-color-one);
  line-height: 56px;
  z-index: 3;
  position: absolute;
  background-color: #fff;
}

.logo-img {
    background: rgba(255, 255, 255, 0) url(../img/logo-title.png) center /cover no-repeat;
    width: 200px;
    height: 40px;
    margin-top: 15px;
  }

nav {
  display: flex;
}

header a {
  font-size: 20px;
  font-weight: 500;
  border-radius: 5px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
  transition: color 0.25s ease-in-out;
  padding-left: 8px;

  &:hover {
    text-decoration: none;
  }
}

/*Main*/

.hero-image {
  background: url(../img/hero-image.jpg) center / cover no-repeat;
  width: 100%;
  height: 500px;
}

.hero-image:before {
    position: absolute;
    background-color: #00000036;
    z-index: 1;
    content: "";
    height: 500px;
    width: 100%;
  }

.hero-text {
    position: absolute;
    z-index: 2;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 50px;
    color: #fff;
}

h1 {
  font-weight: 700;
  padding: 50px 0px 10px 0px;
}


h1 + p {
  font-size: 18px;
  font-weight: 400;
}


h4, p {
  padding: 5px;
  font-size: 18px;
  font-weight: 300px;
}

button{
  width: 120px;
  height: 45px;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  transition: color 0.25s ease-in-out;
  line-height: 26px;

  &:hover {
    text-decoration: none;
  }

}

button + button {
    margin-left: 10px;
}

.primary-button {
    color: #fff;
    background-color: #414d4f;
    &:hover {
      background-color: #414d4fcc;
    }
}

.secondary-button {
    background-color: #fff;
    color: #000;
    border: 0.5px solid #414d4f;
    &:hover {
      background-color: #ffffffcc;
    }
  }


/*Gallery*/

.gallery { 
  margin-top: 50px;
  display: flex;
  gap: 20px;
}

.gallery-images {
    background-size: cover;
    background-position: center;
    width: 200px;
    height: 350px;
}   

/*Footer*/
footer{
  height: 56px;
  padding-top: 10px;
  color: #000;
}

